<template>
  <div class="contacts cont" ref="contacts" :key="keyN">
    <button class="cont__burger" 
      :style="{ top: position, backgroundImage: `url(${icon})` }"
      @click="clicked"
    ></button>
    <div class="cont__menu" v-if="isOpen">
      <transition-group name="contacts" appear>
        <mail :key="1"    style="--i: 1" />
        <git :key="2"     style="--i: 2" />
        <telega :key="3"  style="--i: 3" />
        <phone :key="4"   style="--i: 4" />
      </transition-group>
    </div>
  </div>
</template>

<script>
import mail from '@/components/contacts/contact-gmail.vue'
import git from '@/components/contacts/contact-github.vue'
import telega from '@/components/contacts/contact-telega.vue'
import phone from '@/components/contacts/contact-phone.vue'
export default {
  name: 'head-section-contacts',
  components: { mail, git, telega, phone },
  data() {
    return {
      coord: '',
      isOpen: true,
      keyN: 100
    }
  },
  computed: {
    position() { return this.coord - 50 + 'px' },
    icon() { return this.isOpen ? this.url('cross.svg') : this.url('burger.svg') },
  },
  methods: {
    url(el) { return require('@/assets/svg/' + el) },
    clicked() {
      this.isOpen = !this.isOpen
      this.$emit('clicked') 
    },
    getCoord(el) { return el.getBoundingClientRect().y }
  },
  mounted() {
    const el = this.$refs.contacts
    this.coord = this.getCoord(el)
    if (el) window.addEventListener('resize', (ev) => {
        console.log(ev.target.screen.width)
        this.coord = this.getCoord(el)
        const wx = ev.target.screen.width
        // if (wx >= 990 && wx <= 994) this.keyN += 10
        this.$emit('resize', wx)
      }
    )
  }
}
</script>

<style lang="scss" scoped>
.contacts, .cont {
  // BURGER
  &__burger {
    width: 45px;
    height: 45px;
    background-repeat: no-repeat;
    background-position: center;
    @include media('max', 'md') { display: block; }
    @include media('min', 'md') { display: none; }
    position: absolute;
    left: calc(50% - 18px);
    transition: all 0.3s ease;
  }
  // END BURGER

  &__menu {
    background-color: $grey-80;
    @include media('min', 'md') {
      @include fr;
      padding: 0;
      margin: 0;
    }
    @include media('min', 'lg') {
      // @include fr;
      // outline: 2px solid yellow;
      padding: 0 18% 0 0;
    }
  }

  .contacts-enter-active {
    transition: all 0.4s ease;
    transition-delay: calc(0.1s * var(--i));
  }
  .contacts-enter-from {
    transform: scale(0.5);
    opacity: 0.5;
  }

}
</style>